/* Contenedor principal de la barra de navegación */
.horarios-container_nav {
    display: flex;
    max-width: 25%;
    min-width: 300px;
    position: absolute;
    justify-content: space-around;
    align-items: center;
    top: 5px;
    left: 10px;
    background-color: #9db9d1;
    padding: 10px 15px;
    box-shadow: 4px 4px 4px rgba(32, 32, 32, 0.1);
    border-radius: 10px;
       
}

/* Contenedor de la imagen de perfil */
.horarios-container_nav img {
    border-radius: 50%;
    width: 100px;
    height:100px;
    object-fit: cover;
    margin: 10px;
    
}

/* Contenedor de texto */
.horarios-container_nav div {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

/* Título de bienvenida */
.horarios-container_nav h1 {
    font-size: 24px;
    font-weight: 600;
    margin: 0;
    color: #333;
}

/* Nombre del usuario */
.horarios-container_nav h2 {
    font-size: 20px;
    margin: 5px;
    }

/* Estilo del botón de cierre de sesión */
.horarios-container_nav button {
    align-items: end;
    padding: 4px 8px;
    font-size: 16px;
    background-color: #007BFF;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

/* Efecto hover en el botón */
.horarios-container_nav button:hover {
    background-color: #0056b3;
}

/* Estilos para el contenedor flex */
.horarios-container_nav div {
    display: flex;
    align-items: center;
}
