.comment-box {
    width: 300px;
    height: 150px;
    border: 2px solid #ccc;
    border-radius: 8px;
    background-color: white;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    cursor: grab;
    padding: 10px;
    z-index: 1000;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  textarea {
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    resize: none;
    font-size: 16px;
    padding: 5px;
    font-family: Arial, sans-serif;
  }
  