.reporte-container {
    margin: 50px;
    font-family: Arial, sans-serif;
  }
  
  .form-button {
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .form-button:disabled {
    background-color: #6c757d;
    cursor: not-allowed;
  }
  
  .error-message {
    color: red;
    margin-top: 10px;
  }
  
  .tabla-container {
    margin-top: 20px;
  }
  
  .horarios-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
  }
  
  .horarios-table th,
  .horarios-table td {
    border: 1px solid black;
    padding: 8px;
    text-align: center;
  }
  
  .no-empleados {
    text-align: center;
    font-style: italic;
  }
  